    }
  },
  methods:{
 quit(){
      this.$confirm('确定要退出系统吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '退出成功！'
        });
        this.$router.push('/');
      }).catch(() => {
      });
    },
  },
  mounted() {
   this.$axios({
    method:"get",
    url:"https://www.wudada.online/Api/SjTx",
   }).then((result)=>{
      this.url = result.data.data
   })
  }

}
</script>

